import { FormControl, FormGroup, Validators } from '@angular/forms';

import { EnrollRequestConfig } from './enroll-request.config';

export class EnrollRequestViewModel {
  public form: FormGroup;
  public isKeyboardShown = false;
  public enrollRequestConfig = EnrollRequestConfig;

  public initForm(phoneNumber: string, phoneNumberPattern: RegExp) {
    this.form = new FormGroup({
      phoneNumber: new FormControl(phoneNumber, [
        Validators.required,
        Validators.pattern(phoneNumberPattern),
      ]),
      sendMessage: new FormControl(false, [Validators.requiredTrue]),
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  public get phoneNumberFormControl() {
    return this.form.get('phoneNumber');
  }

  public get sendMessageFormControl() {
    return this.form.get('sendMessage');
  }

  public onInputFocus(isKeyboardShown: boolean) {
    this.isKeyboardShown = isKeyboardShown;
  }
}
