import { ParamMap } from '@angular/router';
import { TokenAccess } from '@nequi/features/security/auth/core';

export class EnrollTokenViewModel {
  public phoneNumber = '';
  public oneTimePin = '';
  public isEnroll: boolean;
  public tokenAccess: TokenAccess;

  public setQueryParams(queryParamMap: ParamMap) {
    this.tokenAccess = queryParamMap.get('tokenAccess') as TokenAccess;
    this.phoneNumber = queryParamMap.get('phoneNumber');
    this.isEnroll =
      queryParamMap.get('isEnroll') === undefined ? true : Boolean(queryParamMap.get('isEnroll'));
  }
}
