import { Component, Injector } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Colors, StatusBarService } from '@nequi/apps/base/mobile/config/features/commons/services';
import { CacheInteractor } from '@nequi/cache/core';
import { CoreException } from '@nequi/commons/exceptions';
import { AuthProvider, PhoneNumberConfigProvider, UserInfoProvider } from '@nequi/features/commons';
import {
  AnalyticsInteractor,
  AnalyticsThirdPartyInteractor,
  AnalyticsType,
  UserEntity
} from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  SessionProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import { UserStatus } from '@nequi/features/contacts/core';
import { ContingencyService } from '@nequi/features/contingency/presentation';
import {
  EnrollInteractor,
  ExcededAttempsGenerateOtpException,
  IsInvitationModeException,
  UserExistException
} from '@nequi/features/enroll/core';
import { ProfileInteractor } from '@nequi/features/profile/core';
import { ProfileService } from '@nequi/features/profile/presentation';
import { AuthInteractor, TokenAccess } from '@nequi/features/security/auth/core';
import { SettingInteractor } from '@nequi/features/settings/core';
import { LoadingService, ToastService } from '@nequi/ui';

import { ChangePhoneNumberService } from '../../services/change-phone-number.service';
import { EnrollRequestConfig } from './enroll-request.config';
import { EnrollRequestViewModel } from './enroll-request.view-model';

const CONECTION_ERROR_1 = '1001';
const CONECTION_ERROR_2 = '1002';

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
function isErrorConection(errorCode: string): boolean {
  return errorCode === CONECTION_ERROR_1 || errorCode === CONECTION_ERROR_2;
}

@Component({
  selector: 'app-enroll-request',
  templateUrl: './enroll-request.page.html',
  styleUrls: ['./enroll-request.page.scss'],
})
export class EnrollRequestPage extends FeatureBasePage {
  public viewModel = new EnrollRequestViewModel();
  public sendRequestErrors = {
    [UserExistException.name]: () => this.userExist(),
    [ExcededAttempsGenerateOtpException.name]: () => this.userExceededAttempts(),
    [IsInvitationModeException.name]: () => this.invitationMode(),
  };

  constructor(
    injector: Injector,
    private authProvider: AuthProvider,
    private settingInteractor: SettingInteractor,
    private translateProvider: TranslateProvider,
    private loadingService: LoadingService,
    private enrollInteractor: EnrollInteractor,
    private toastService: ToastService,
    private alertService: AlertService,
    private profileService: ProfileService,
    private router: Router,
    private statusBarService: StatusBarService,
    private cacheInteractor: CacheInteractor,
    private userInfoProvider: UserInfoProvider,
    private changePhoneNumberService: ChangePhoneNumberService,
    private analyticsInteractor: AnalyticsInteractor,
    private profileInteractor: ProfileInteractor,
    private activatedRoute: ActivatedRoute,
    private sessionProvider: SessionProvider,
    private contingencyService: ContingencyService,
    private authInteractor: AuthInteractor,
    private clevertapInteractor: AnalyticsThirdPartyInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private phoneNumberConfig: PhoneNumberConfigProvider
  ) {
    super(injector);
    this.statusBarService.changeStatusBarColor(Colors.DEFAULT);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    const phoneNumber = this.router.getCurrentNavigation().extras.state?.phoneNumber;

    this.viewModel.initForm(phoneNumber, this.phoneNumberConfig.validExpression);
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public async sendRequest() {
    try {
      if (this.sessionProvider.hasContingency()) {
        this.contingencyService.showCanNotCreateAccountModal();

        return;
      }

      await this.loadingService.show();

      await this.handleSendRequest();
    } catch (error) {
      this.handleError(error);
    }
  }

  public async handleSendRequest() {
    const phoneNumber: number = this.viewModel.phoneNumberFormControl.value;

    if (this.changePhoneNumberService.isChangePhoneNumberActive) {
      await this.validateNewPhoneNumber(phoneNumber.toString());
    } else {
      await this.enrollInteractor.request(phoneNumber);
      await this.loadingService.hideWithoutFeedback();
      this.trackAskForNumber();
      this.goToEnrollToken();
    }
  }

  public async invitationMode() {
    await this.loadingService.hideWithoutFeedback();
    // TODO: $state.go('enrollInvitationMode', {});
    this.goToEnrollToken();
  }

  private goToEnrollToken() {
    this.navigatorProvider.navigateForward(this.viewModel.enrollRequestConfig.enrollTokenUrl, {
      queryParams: {
        phoneNumber: this.viewModel.phoneNumberFormControl.value,
        tokenAccess: TokenAccess.REGISTRY,
        isEnroll: true,
      },
    });
  }

  private handleError(error: CoreException) {
    if (this.sendRequestErrors[error?.constructor?.name]) {
      return this.sendRequestErrors[error.constructor.name]();
    }

    this.genericViewError(error);
  }

  private async genericViewError(error) {
    await this.loadingService.hideWithoutFeedback();

    await this.toastService.hide();
    const isConectionError = isErrorConection(error.code);

    if (isConectionError) {
      return this.toastService.showConnectionError(error.message);
    }

    this.toastService.showError(error.message);
  }

  private async userExceededAttempts() {
    await this.loadingService.hideWithoutFeedback();

    this.alertService.showByI18n({
      i18nKey: this.viewModel.enrollRequestConfig.errorExceededAttemptsGenerateOtp,
      handlers: {
        accept: () => this.authProvider.signOut(),
      },
    });
  }

  private async accountAlreadyExists() {
    await this.settingInteractor.get();

    /* analyticsProvider.removeAditionalTagForView(
      configAnalitycsProvider.generalAnalytics.aditionalTagsForViews.userOrigin.tagName
    ); */

    this.changePhoneNumberService.isChangePhoneNumberActive = false;

    this.navigatorProvider.navigateForward(
      this.viewModel.enrollRequestConfig.authValidatePasswordUrl,
      {
        queryParams: {
          phoneNumber: this.viewModel.phoneNumberFormControl.value,
          isDeviceProvisioned: false,
        },
      }
    );
  }

  private async showAlertAccountAlreadyExists() {
    const phoneNumber = await this.userInfoProvider.getPhoneNumber();

    this.alertService.showByI18n({
      i18nKey: this.viewModel.enrollRequestConfig.errorAccountAlreadyExists,
      handlers: {
        accept: async () => {
          this.accountAlreadyExists();
        },
      },
      messageParameters: [phoneNumber],
    });
  }

  private async validateStateUser(response: UserEntity) {
    this.cacheInteractor.clearGroup(this.moduleConfigProvider.cache.keys.userProfileInfo);
    this.profileService.setLastLogin(null);

    switch (response.state) {
      case UserStatus.ENROLL:
        this.goToEnrollToken();
        break;
      case UserStatus.LITE_REGISTRY:
        this.showAlertAccountAlreadyExists();
        break;
      default:
    }
  }

  private async userExist() {
    try {
      const response = await this.authInteractor.phoneNumberEmailValidation(
        this.viewModel.phoneNumberFormControl.value
      );

      this.clevertapInteractor.changeUserIdentity(response.cifId);

      await this.validateStateUser(response);
      await this.loadingService.hideWithoutFeedback();
    } catch (catchError) {
      await this.loadingService.hideWithoutFeedback();
      this.toastService.showInfo(catchError.message);
    }
  }

  private trackAskForNumber() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: EnrollRequestConfig.anaytics.askForNumberEventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: EnrollRequestConfig.anaytics.askForNumberEventName },
    });
  }

  private async validateNewPhoneNumber(phoneNumber: string) {
    try {
      await this.profileInteractor.validateNewPhoneNumber(phoneNumber);
      await this.loadingService.hideWithoutFeedback();
      this.navigatorProvider.navigateForward(this.viewModel.enrollRequestConfig.enrollTokenUrl, {
        queryParams: {
          phoneNumber: phoneNumber,
        },
        relativeTo: this.activatedRoute,
      });
    } catch (error) {
      this.handleError(error);
    }
  }
}
