import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ModuleWithProvidersFactory, ORIGIN_FLOW, OriginFlow } from '@nequi/features/commons';

import { EnrollRequestPage } from './pages/enroll-request/enroll-request.page';
import { EnrollTokenPage } from './pages/enroll-token/enroll-token.page';

const routes: Routes = [
  {
    path: 'enroll-request',
    component: EnrollRequestPage,
  },
  {
    path: 'enroll-token',
    component: EnrollTokenPage,
  },
  {
    path: 'data-treatment',
    loadChildren: () =>
      import('@nequi/features/data-treatment/presentation').then(
        (m) =>
          new ModuleWithProvidersFactory(
            m.FeaturesDataTreatmentModule.withConfig([
              { provide: ORIGIN_FLOW, useValue: OriginFlow.ENROLL },
            ])
          )
      ),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeaturesEnrollRoutingModule {}
