import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import {
  AnalyticsInteractor,
  AnalyticsType,
  ProvisioningFailedException,
  ProvisioningInteractor
} from '@nequi/features/commons/core';
import {
  ActivitySessionService,
  FeatureBasePage,
  RequestOtpOrganism
} from '@nequi/features/commons/presentation';
import { ProvisioningManagementService } from '@nequi/features/commons/presentation/services';
import {
  AuthProvider,
  ModuleConfigProvider,
  SessionProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import { UserStatus } from '@nequi/features/contacts/core';
import {
  EnrollInteractor,
  OtpChannel,
  ValidateOtpEntity,
  ValidateOtpParams
} from '@nequi/features/enroll/core';
import { TokenAccess } from '@nequi/features/security/auth/core';
import { BiometryType } from '@nequi/features/security/biometry/core/constants';
import { LoadingService, ToastService } from '@nequi/ui';

import { ChangePhoneNumberService } from '../../services/change-phone-number.service';
import { TokenAccessActionService } from '../../services/token-access-action.service';
import { EnrollTokenConfig } from './enroll-token.config';
import { EnrollTokenViewModel } from './enroll-token.view-model';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

@Component({
  selector: 'app-enroll-token',
  templateUrl: './enroll-token.page.html',
})
export class EnrollTokenPage extends FeatureBasePage implements OnInit {
  @ViewChild(RequestOtpOrganism) public requestOtpOrganism: RequestOtpOrganism;

  public viewModel = new EnrollTokenViewModel();

  protected route: ActivatedRoute;
  protected sessionProvider: SessionProvider;
  protected loadingService: LoadingService;
  protected translateProvider: TranslateProvider;
  protected activitySessionService: ActivitySessionService;
  protected enrollInteractor: EnrollInteractor;
  protected toastService: ToastService;
  protected provisioningManagementService: ProvisioningManagementService;
  protected provisioningInteractor: ProvisioningInteractor;
  protected userInfoProvider: UserInfoProvider;
  protected tokenAccessActionService: TokenAccessActionService;
  protected authProvider: AuthProvider;
  protected changePhoneNumberService: ChangePhoneNumberService;
  protected analyticsInteractor: AnalyticsInteractor;
  protected applicationDataInteractor: ApplicationDataInteractor;
  protected moduleConfigProvider: ModuleConfigProvider;

  constructor(injector: Injector) {
    super(injector);

    this.route = injector.get(ActivatedRoute);
    this.sessionProvider = injector.get(SessionProvider);
    this.loadingService = injector.get(LoadingService);
    this.translateProvider = injector.get(TranslateProvider);
    this.activitySessionService = injector.get(ActivitySessionService);
    this.enrollInteractor = injector.get(EnrollInteractor);
    this.toastService = injector.get(ToastService);
    this.provisioningManagementService = injector.get(ProvisioningManagementService);
    this.provisioningInteractor = injector.get(ProvisioningInteractor);
    this.userInfoProvider = injector.get(UserInfoProvider);
    this.tokenAccessActionService = injector.get(TokenAccessActionService);
    this.authProvider = injector.get(AuthProvider);
    this.changePhoneNumberService = injector.get(ChangePhoneNumberService);
    this.analyticsInteractor = injector.get(AnalyticsInteractor);
    this.applicationDataInteractor = injector.get(ApplicationDataInteractor);
    this.moduleConfigProvider = injector.get(ModuleConfigProvider);

    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.accountLocksKey);
  }

  public async ngOnInit() {
    this.viewModel.setQueryParams(this.route.snapshot.queryParamMap);

    await this.tokenAccessActionService.initActions(
      this.viewModel.tokenAccess,
      this.viewModel.phoneNumber
    );
  }

  public async goBack() {
    this.requestOtpOrganism?.cancel();
  }

  public onCanceled() {
    if (this.viewModel.isEnroll) {
      return this.navigatorProvider.navigateRoot(EnrollTokenConfig.routes.enrollRequest);
    }

    this.navigatorProvider.navigateRoot(EnrollTokenConfig.routes.login);
  }

  public async onOtpFilled({ otp, channel }: { otp: string; channel: OtpChannel }): Promise<void> {
    if (this.changePhoneNumberService.isChangePhoneNumberActive) {
      await this.changePhoneNumberService.changePhoneNumber(this.viewModel.phoneNumber, otp);

      return this.tokenAccessActionService.finishActions(null);
    }

    const params: ValidateOtpParams = {
      phoneNumber: this.viewModel.phoneNumber,
      isEnroll: this.viewModel.isEnroll,
      channel,
      otp,
    };

    try {
      await this.loadingService.show();
      const result = await this.enrollInteractor.validateOtp(params);

      await this.otpValidated(result);

      await this.loadingService.hide(true);

      await this.checkBiometryBlocked();
    } catch (error) {
      await this.loadingService.hide(false);

      if (error instanceof ProvisioningFailedException) {
        return this.showProvisioningFailedError();
      }

      this.requestOtpOrganism.processError(error);
    }
  }

  public async otpValidated(response: ValidateOtpEntity) {
    this.trackRegistryOtp();

    if (response.state === UserStatus.ENROLL) {
      this.activitySessionService.initActivity();
      this.sessionProvider.userHasSession = true;

      await this.userInfoProvider.save({
        phoneNumber: this.viewModel.phoneNumber,
        clientId: null,
        state: String(response.state),
      });

      this.sendAnalyticsAcceptDataTreatment();
    } else {
      if (typeof response === 'undefined' || typeof response.softToken === 'undefined') {
        return this.showProvisioningFailedError();
      }

      await this.provisioningDevice(response.softToken);
    }

    await this.loadingService.hide(true);
    await this.tokenAccessActionService.finishActions(this.viewModel.tokenAccess);
  }

  public async generateVoiceOtp() {
    try {
      await this.loadingService.show();
      await this.enrollInteractor.generateVoiceOtp(parseInt(this.viewModel.phoneNumber, 10));
      await this.loadingService.hide(true);
    } catch (error) {
      await this.loadingService.hide(false);
      this.toastService.showError(error.errorMessage);
    }
  }

  private async checkBiometryBlocked() {
    const biometryBlocked = await this.applicationDataInteractor.findValue<string>(
      this.moduleConfigProvider.applicationData.biometryBlocked,
      null,
      this.userInfoProvider.getPhoneNumber()
    );

    if (biometryBlocked !== null) {
      if (biometryBlocked === BiometryType.FACE) {
        this.tokenAccessActionService.finishActions(TokenAccess.RESTART_BIOMETRY_FACE);

        return;
      }

      this.tokenAccessActionService.finishActions(TokenAccess.RESTART_BIOMETRY_VOICE);
    }
  }

  private async provisioningDevice(softToken: string) {
    try {
      this.sessionProvider.userHasSession = true;
      await this.provisioningManagementService.registerByActivationCode(softToken);

      await this.provisioningInteractor.activateDevice({
        phoneNumber: this.viewModel.phoneNumber,
        isEmailSent: true,
      });
    } catch (error) {
      this.authProvider.signOut();

      throw error;
    }
  }

  private showProvisioningFailedError() {
    this.toastService.showError(
      this.translateProvider.get(EnrollTokenConfig.errorMessages.provisioningFailedError)
    );
  }

  private trackRegistryOtp() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: EnrollTokenConfig.anaytics.registryOtpEventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: EnrollTokenConfig.anaytics.registryOtpEventName },
    });
  }

  private sendAnalyticsAcceptDataTreatment() {
    const analytics = EnrollTokenConfig.anaytics.acceptDataTreatment;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: analytics.name,
        eventProps: stringsArrayToObjectWithValues(analytics.params, [getFormattedDateTimes()]),
      },
    });
  }
}
