import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { FeaturesCommonsModule } from '@nequi/features/commons/presentation';
import { PhonePipe } from '@nequi/features/commons/presentation/';
import { EnrollInteractor, EnrollRepository } from '@nequi/features/enroll/core';
import { NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { EnrollConfig } from '../config/enroll.config';
import { FeaturesEnrollRoutingModule } from './features-enroll-routing.module';
import { EnrollRequestPage } from './pages/enroll-request/enroll-request.page';
import { EnrollTokenPage } from './pages/enroll-token/enroll-token.page';
import { TokenAccessActionService } from './services/token-access-action.service';

@NgModule({
  declarations: [EnrollRequestPage, EnrollTokenPage],
  imports: [
    CommonModule,
    FeaturesEnrollRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule,
    TranslateModule,
    NequiUiModule,
    FeaturesCommonsModule,
  ],
  providers: [
    TokenAccessActionService,
    PhonePipe,
    {
      provide: EnrollInteractor,
      deps: [EnrollRepository],
      useClass: EnrollInteractor,
    },
    {
      provide: ModuleConfigProvider,
      useValue: EnrollConfig,
    },
  ],
})
export class FeaturesEnrollModule {}
